<template>
    <div class="shopList">
        <head-top></head-top>
       <el-main>
    <!-- 账号列表 -->
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span>商家列表</span>
                    <router-link to="/addShop">
                        <el-button style="float: right; padding: 3px 0" type="text">添加</el-button>
                    </router-link>
                </div>
                <div class="text item">
                <!-- 表格 -->
                    <el-table :data="tableData" style="width: 100%">
                        <el-table-column prop="username" label="店铺名称" width="180"> </el-table-column>
                        <el-table-column prop="realname" label="店铺地址" width="180"> </el-table-column> 
                        <el-table-column prop="idType" label="店铺介绍"> </el-table-column>
                        <el-table-column fixed="right" label="操作" width="100"> 
                            <template slot-scope="scope">
                                <el-button type="text" @click="dialogFormVisible = true">修改</el-button>
                                <el-button @click.native.prevent="handleDel(scope.$index, tableData)" type="text" size="small">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-dialog title="商家" :visible.sync="dialogFormVisible">
