                            <el-form-item label="店铺名称" :label-width="formLabelWidth">
                            <el-input v-model="form.name" auto-complete="on"></el-input>
                            </el-form-item>
                            <el-form-item label="店铺地址" :label-width="formLabelWidth">
                            <el-input v-model="form.side" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="店铺介绍" :label-width="formLabelWidth">
                            <el-input v-model="form.introduce" auto-complete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false">取 消</el-button>
                            <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                        </div>
                    </el-dialog>
                </div>
            </el-card>
        </el-main>
