import Vue from 'vue';
import Router from 'vue-router';
import Manage from './page/manage';
import login from './page/login';
import home from './page/home';
import userList from './page/userList';
import shopList from './page/shopList';
import foodList from './page/foodList';
import orderList from './page/orderList';
import adminList from './page/adminList';
import addShop from './page/addShop';
import addGoods from './page/addGoods';
import visitor from './page/visitor';
import vueEdit from './page/vueEdit';
import adminSet from './page/adminSet';
import explain from './page/explain';


Vue.use(Router);

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/manage',
      name: '',
      component: Manage,
      children: [{
        path: '',
        name: 'home',
        component: home,
        meta: []
        },{
          path:'/userList',
          name: 'userList',
          component: userList,
          meta: ['数据管理','用户列表']
        },{
          path:'/shopList',
          name: 'shopList',
          component: shopList,
          meta: ['数据管理','商家列表']
        },{
          path:'/foodList',
          name: 'foodList',
          component: foodList,
          meta: ['数据管理','食品列表']
        },{
          path:'/orderList',
          name: 'orderList',
          component: orderList,
          meta: ['数据管理','订单列表']
        },{
          path:'/adminList',
          name: 'adminList',
          component: adminList,
          meta: ['数据管理','管理员列表']
        },{
          path:'/addShop',
          name: 'addShop',
          component: addShop,
          meta: ['数据管理','添加商铺']
        },{
          path:'/addGoods',
          name: 'addGoods',
          component: addGoods,
          meta: ['数据管理','添加商品']
        },{
          path:'/visitor',
          name: 'visitor',
          component: visitor,
          meta: ['数据管理','用户分布']
        },{
          path:'/vueEdit',
          name: 'vueEdit',
          component: vueEdit,
          meta: ['数据管理','文本编辑']
        },{
          path:'/adminSet',
          name: 'adminSet',
          component: adminSet,
          meta: ['数据管理','管理员设置']
        },{
          path:'/explain',
          name: 'explain',
          component: explain,
          meta: ['数据管理','说明']
      }]
    }]
});
